body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  word-break: break-all;
}

.screen {
  height: 30rem;
  background-color: #282923;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 350px) {
  .screen {
    height: 15rem;
  }
}
.screen .title {
  color: #fff;
  font-size: 4rem;
  font-weight: bold;
}
@media screen and (max-width: 1000px) {
  .screen .title {
    font-size: 2.5rem!important;
  }
}
@media screen and (max-width: 350px) {
  .screen .title {
    font-size: 2rem!important;
  }
}
.screen .intro {
  color: #f92372;
  font-size: 2rem;
}
@media screen and (max-width: 1000px) {
  .screen .intro {
    font-size: 1.5rem!important;
  }
}
@media screen and (max-width: 350px) {
  .screen .intro {
    font-size: 1rem!important;
  }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main {
  width: 100%;
}
.main .center {
  margin: 0 auto;
}
.main .subtitle {
  font-size: 1.2rem;
  font-weight: bold;
}
.main .screen .title {
  color: #fff;
  font-size: 6rem;
  font-weight: bold;
}
.main .screen .intro {
  color: #f92372;
  font-size: 3rem;
}
.main .desc {
  padding: 1rem;
  background-color: #f6f6f6;
}
.main .episodes {
  padding: 1rem;
}
.main .episodes .episode {
  padding: 1rem 0;
}
.main .episodes .episode .title {
  font-weight: bold;
  text-decoration: none;
  box-shadow: 0 1px rgba(51,122,183,0.2);
  color: #7eaf22;
}
.main .episodes .episode .title:hover {
  color: #a3e22c;
}
.main .episodes .episode .intro {
  color: #74705b;
  font-size: 0.8rem;
  margin-bottom: 10px;
}

.nav-bar {
  width: 100%;
}

.btns {
  text-align: center;
  padding: 1rem;
}
.btns a button {
  width: 100%;
}
.btns a + a {
  margin-left: 10px;
}
@media (max-width: 600px) {
  .btns a button {
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
  }
}

.episode {
  width: 100%;
}
.episode .center {
  margin: 0 auto;
}
.episode .subtitle {
  font-size: 1.2rem;
  font-weight: bold;
}
.episode .audio {
  background-color: #000;
}
.episode .desc {
  padding: 1rem;
  background-color: #f6f6f6;
}

.about {
  width: 100%;
}
.about .center {
  margin: 0 auto;
}
.about .subtitle {
  font-size: 1.2rem;
  font-weight: bold;
}
.about .screen .intro {
  color: #74705b;
  font-size: 1.2rem;
}
.about .desc {
  padding: 1rem;
  background-color: #f6f6f6;
}

.android {
  width: 100%;
}
.android .center {
  margin: 0 auto;
}
.android .subtitle {
  font-size: 1.2rem;
  font-weight: bold;
}
.android .screen .intro {
  color: #74705b;
  font-size: 1.2rem;
}
.android .desc {
  padding: 1rem;
  background-color: #f6f6f6;
}

